export function extractData(config) {
    var result = Object.keys(config).reduce(function (curr, key, i) {
        curr[key] = config[key][0];
        return curr;
    }, {});
    return result;
}
export function getFormInputs(form) {
    if (!form) {
        return [];
    }
    return Array.from(form.querySelectorAll('input, select, textarea') || []);
}
export function getInputElements(form, prop) {
    if (!form) {
        return null;
    }
    var elements = Array.from(form.querySelectorAll("[name=\"".concat(prop, "\"]")));
    if (!elements.length) {
        return null;
    }
    return elements;
}
export function updateValuesForFormInputs(form, data) {
    if (!form) {
        return {};
    }
    var newRefs = {};
    Object.keys(data).forEach(function (prop) {
        var els = getInputElements(form, prop);
        if (!els) {
            return;
        }
        newRefs[prop] = els;
        switch (els[0].type) {
            case 'checkbox':
                els[0].checked = Boolean(data[prop]);
                break;
            case 'radio':
                els.forEach(function (f) { return (f.checked = f.value === data[prop]); });
                break;
            case 'file':
                //file cannot be set programmatically
                els[0].value = '';
                break;
            case 'submit':
            case 'reset':
            case 'image':
                break;
            default:
                els[0].value = String(data[prop]);
                break;
        }
    });
    return newRefs;
}
export function validateFn(config, data) {
    var errors = {};
    Object.keys(config).forEach(function (prop) {
        var validators = config[prop][1];
        var valid = true;
        (validators || []).forEach(function (validator) {
            if (typeof validator === 'function') {
                var r = validator.call(null, data[prop], data);
                if (valid && r) {
                    valid = false;
                    errors[prop] = r;
                }
            }
        });
    });
    var valid = Object.keys(errors).length === 0;
    return { valid: valid, errors: errors };
}
